import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { useEffect, useState } from "react";
import { RiUser3Fill } from "react-icons/ri";
import { FaIdCard } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import Nav from "../../components/Navbar/Nav";
import FallBackLoader from "../../components/FallBack/FallBackLoader";
const Users = () => {
  const [allfeedbacks, Setallfeedbacks] = useState([]);
  const [loading, SetLoading] = useState(false);

  const getAllfeedback = async () => {
    try {
      SetLoading(true);
      const { data } = await axios.get("/api/user/allfeedback");
      if (data?.success) {
        Setallfeedbacks(data.feedbacks);
        toast.success(data.message);
        SetLoading(false);
      } else {
        toast.error(data.message);
        SetLoading(false);
      }
    } catch (error) {
      SetLoading(false);
      toast.error("something went wrong");
    }
  };

  useEffect(() => {
    getAllfeedback();
  }, []);

  return (
    <div>
      {loading ? (
        <FallBackLoader />
      ) : (
        <>
          {" "}
          <Nav />
          <div className="admin_main p-2">
            <hr />
            <div className="d-flex row ">
              <div className="col-lg-2 col-sm-12 ">
                <AdminMenu />
              </div>
              <div className="col-lg-10 col-sm-12   ">
                <div className=" px-1 w-95 ">
                  <div>
                    <div className="d-flex row gap-1">
                      {allfeedbacks
                        .map((data) => (
                          <>
                            <div className="card" style={{ width: "18rem" }}>
                              <div className="card-body" key={data._id}>
                                <h5 className="card-text text-capitalize ">
                                  <RiUser3Fill className="me-1 h-2" />{" "}
                                  {data.username || data.fullname}
                                </h5>
                                <hr />

                                <span>
                                  {" "}
                                  <MdEmail className="me-1" />
                                  {data.email}
                                </span>
                                <p className="bg-warning text-capitalize  p-1 ps-2 rounded-1">
                                  {" "}
                                  {data.message}
                                </p>
                                <hr />

                                <span>
                                  {" "}
                                  <FaIdCard className="me-1" />
                                  {data._id}
                                </span>
                              </div>
                            </div>
                          </>
                        ))
                        .reverse()}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default Users;
